.game_name{font-family:'Press Start 2P';color:white;z-index:6}.rotate{-webkit-animation:rotation 4s infinite;animation:rotation 4s infinite}@font-face{font-family:'Press Start 2P';src:url("https://fonts.googleapis.com/css?family=Press+Start+2P")}@-webkit-keyframes rotation{from{transform:rotate(0deg)}to{transform:rotate(359deg)}}@keyframes rotation{from{transform:rotate(0deg)}to{transform:rotate(359deg)}}.bone{width:50px;z-index:6}.name_container{display:flex;border:1px solid black;background-color:#FFCB37;justify-content:center;z-index:6}#main{display:block;margin:auto;position:absolute;top:0;left:0;bottom:0;right:0;z-index:5;padding:40px;background-color:transparent}#main>p{font-family:'Press Start 2P';display:block;text-align:center;padding-top:40px}.button{display:block;width:300px;margin:0 auto;height:60px;font-family:'Press Start 2P';line-height:20px;border:1px solid #AA2666;color:white;font-weight:bold;background:linear-gradient(to bottom, #3cf 0%, #f9c 100%);border-radius:5px}.button:hover{background-color:#B30D5D;background-image:linear-gradient(to bottom, #B30D5D 0%, #FB1886 100%)}.game_area{position:relative;height:300px;width:800px;margin:auto;display:block;border:3px solid black;z-index:2}#canvas_container{position:absolute;z-index:2}#main_background{position:absolute;z-index:0}br{content:" ";padding:20px}.nav_bar{padding:20px;display:flex;justify-content:space-around;width:800px;border:1px solid black;margin:0 auto;box-shadow:inset 0 0 0 10px #FFCB37, inset 0 0 0 14px #fff}.difficulty_button{font-family:'Press Start 2P';padding:5px 10px;border:0.16em solid  #FFCB37;border-radius:2em}.difficulty_button:hover{border-color:#fff}
